import * as ngMaterial from 'angular-material';
import uiRouter from 'angular-ui-router';
import * as ngMaterialIcons from 'angular-material-icons';

import {initRouter} from './router';
import materialServiceMod from '../../services/material.service';
import restRegMod from '../../services/rest.service';
import * as _ from 'lodash';
import apiValueFunc from './service/salt.api.value';

const module = angular.module("saltModule", [ngMaterialIcons, ngMaterial as string, 'ui.router', materialServiceMod, restRegMod]);
const _headers = {};

module.config([
    "$stateProvider",
    "$urlRouterProvider",
    "RestangularProvider",
    ($stateProvider, $urlRouterProvider, RestangularProvider) => {
        // 初始化路由
        initRouter($urlRouterProvider, $stateProvider);
        // 添加全局拦截器拦截器
        RestangularProvider.addResponseInterceptor((data, operation, what, url, response: restangular.IResponse, deferred)=> {
            console.log(arguments);
            return data;
        });

        RestangularProvider.addFullRequestInterceptor((element: any, operation: string, what: string, url: string, headers: any, params: any, httpConfig: angular.IRequestShortcutConfig)=> {
            return {
                element: element,
                params: params,
                headers: _.extend(_headers, headers),
                httpConfig: httpConfig
            };
        });
    }])
    .run(["$state", "restUtils", "materialUtils", ($state, restUtils: fx.utils.restStatic, materialUtils: fx.utils.materialStatic)=> {
        // 添加全局拦截器拦截器
        // restangular.withConfig((restAngularConfigure: restangular.IProvider)=> {
        //     restAngularConfigure.addResponseInterceptor((data, operation, what, url, response: restangular.IResponse, deferred)=> {
        //         console.log(arguments);
        //         return data;
        //     });
        // });
        //
        // restUtils.setConfig((restAngularConfigure: restangular.IProvider)=> {
        //     restAngularConfigure.addResponseInterceptor((data, operation, what, url, response: restangular.IResponse, deferred)=> {
        //         console.log(arguments);
        //
        //
        //         return data;
        //     });
        // });
    }]);

apiValueFunc(module);

export default module.name;


